import React from 'react'
import './Menu.css'
import icon from'../../../assets/menu.svg'

function Menu(){
    return(

        <div className="div-menu">    
            <input className="checbox-M" id="checbox-M" type="checkbox"/>
            <label for="checbox-M"> <img src={icon}/></label>
       

            <ul className="menu">
                <li><a>Instalations Tutorials</a></li>
                <li><a>Curses</a></li>
                <li><a>Resources</a></li>
                <li><a>About</a></li>
                <li><a>Support</a></li>
                <li><a className="Donate">Donate</a></li>
            
            </ul>

        </div>
    )

}
export default Menu